export default {
  
  created() {
    gsap.registerPlugin(ScrollTrigger);
    gsap.registerPlugin(ScrollToPlugin);
  },

  data() {
    return {
      timeline: {
        to: _to.bind(this),
        from: _from.bind(this),
      },
      helpers: {
        vh: _vh,
        vw: _vw,
        isInViewport: _isInViewport,
        isMobile: _isMobile
      },
      gsap
    }
  },

  methods: {
    toggleBodyClass
  }
}
